<template>
  <div>模板消息</div>
</template>
<script>
export default {
  data() {
    return {
      access_token: "",
    };
  },
  mounted() {
    this.token();
  },
  methods: {
    token() {
      this.$axios.get("/cgi-bin/token", {
        params: {
          grant_type: "client_credential",
          appid: "wx281dd96f24f5b4d2",
          secret: "61b444bb64c82e6cf021410174888952",
        },
      }).then((res) => {
        this.access_token = res.data.access_token;
        console.log(res.data.access_token);
        this.meg()
      });
    },
    meg() {
      this.$axios.post(`/cgi-bin/message/template/send?access_token=${this.access_token}`,
          {
            touser: "o5wEI6A7_oyM_Y-PmtlPt0NPbMY0",
            template_id: "u1nCvLvt8rnmd3djPPFdU5hCcuYLedFcpHSrzJWcnq8",
            url: "https://baidu.com",
            data: {
              thing: {
                value: "停止交易 有内鬼！",
                color: "#fb7299",
              },
            },
          }
        ).then((res) => {
          console.log(res);
        }).catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>